body {
    margin: 0;
    padding: 0;
    font-family: 'Raleway';
    background: red;
    background-repeat: no-repeat;
    /* For browsers that do not support gradients */
    background: -webkit-linear-gradient(left top, #247caf, #24afaf));
    /* For Safari 5.1 to 6.0 */
    background: -o-linear-gradient(bottom right, #247caf, #24afaf));
    /* For Opera 11.1 to 12.0 */
    /* For Firefox 3.6 to 15 */
    background: -webkit-gradient(linear, left top, right bottom, from(#247caf), to(#24afaf));
    background: -webkit-linear-gradient(top left, #247caf, #24afaf);
    background: -o-linear-gradient(top left, #247caf, #24afaf);
    background: linear-gradient(to bottom right, #247caf, #24afaf);
    /* Standard syntax */
}



